.topnav {
  overflow: hidden;
  background-color: #333;
  margin: 0;
  border: 4px solid black;
  justify-content: center;
  text-align: center;

}

.topnav button {
  margin: 0;
  display: inline-block;
  width: 500px;
  border-radius: 0%;
  padding: 14px 16px;
  color: black;
  text-align: center;
  justify-content: center;
  text-decoration: none;
  border: 2px solid grey;
  border-style: outset;
}

.topnav button:hover {
  background-color: #ddd;
  color: black;
  border-style: inset;
}

body {
  border: 0;
  margin: 0;
  padding: 0;
}